.App{text-align:center}.App-header{display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 1.5vmin)}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.characters{list-style:none;padding-left:0;width:100%}.characters li{display:flex;align-items:center;border:solid 2px #d0d0d0;border-radius:0.2em;padding:0.5em 0.8em 0.5em 0.5em;margin-bottom:1em}.characters p{max-width:none;font-weight:bold;margin:0}.characters-thumb{overflow:hidden;flex-shrink:0;width:50%;height:auto;background-color:#e8e8e8;padding:0.5em;margin-right:0.5em}.characters-thumb img{display:block;width:100%;height:auto}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

